.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
.fa-brands{
  color: blue;
}
body{
  display: flex;
  flex-direction: row;
}
.container-1{
  width: 50%;
  height: 100vh;
  background: rgb(61, 38, 235);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.container-2{
  width: 50%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.logo{
   width: 100%;
   height: 20vh;
   display: flex;
   background: white;
   align-items: center;
   justify-content: center;
}
.logo-1{
  width: 70%;
  height: 100%;
  background-image: url(./KIIT-3.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.login{
  width: 100%;
  height: 40vh;
  background: rgb(255, 255, 255);
  color: rgb(62, 62, 62);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.login h1{
  margin-top: 30px;
}
.login input{
  width: 50%;
  height: 50px;
  outline: none;
  border: 1px solid rgb(188, 188, 188);
  border-radius: 10px;
  /* box-shadow: 0px 5px 20px rgb(106, 105, 105); */
  padding-left: 20px;
  margin: 30px 0px 0px 0px;
}
.login input:hover{
  border: 2px solid rgb(43, 43, 255);
  transition-delay: 0.1s;
}
.login p{
  font-size: 20px;
}
.footer{
  width: 100%;
  height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
button{
  width: 40%;
  height: 40px;
  border: none;
  border-radius: 10px;
  outline: none;
  background: rgb(38, 38, 231);
  /* margin-top: 20px 0px 30px 0px; */
}
button a{
  color: white;
  text-decoration: none;
}
.pictures{
  width: 100%;
  height: 60vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}
.image-1{
  width: 30%;
  height: 300px;
  border: 2px solid white;
  border-radius: 10px;
  transform: rotate(-25deg);
}
.image-2{
  width: 25%;
  height: 250px;
  border: 2px solid white;
  border-radius: 10px;
  transform: rotate(25deg);
}
.slogan{
  width: 100%;
  height: 25vh;
}