body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.signupinput{
	float:left; margin:15px; margin-left:20px; font-family: 'Nunito', sans-serif;font-family: 'Roboto', sans-serix;width:40%;margin-right:-5px;}
.bigsignupinput{width: 80%;}

.signupinput > input::placeholder{
  color:rgba(115, 115, 115, 0.7); font-size:10px;
}
.signupinput > span{
 color:rgba(115, 115, 115, 1); font-size:13px;
}
#signupinputbox{background:rgba(250, 252, 255, 1); margin-top:-5px; border-radius:3px; border:none; width:100%; height:42px;box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 1.5px 0px;color:black; font-size:12px; padding-left:10px;}
#lottieone {
  position:'fixed';bottom:'-100px';
  height: 620;z-index: '-1';
}
#signupdiv{position:absolute;top:17.5%;left:5%;width:35%; height:75%; background:white; border-radius:10px;/*box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 3px 0px, rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;*/width:500px;}
.button {
  background-color: #0BB7E8; /* Green */
  border: none;
  color: white;
 
  width:60%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius:25px;
  cursor: pointer;
  margin-left:-10px;
  margin-top:-12px;
}
#regisbtn{float: right; margin:8px; font-family: 'Inter', sans-serif; padding:6px; width:85px; font-size:12px; border:3px solid rgba(39, 49, 82, 1); }
.checkdiv{margin-left:25px;}
@media only screen and (max-width: 600px) {
  #regisbtn{float: right; margin:8px; font-family: 'Inter', sans-serif; padding:6px; width:65px; font-size:9px; border:3px solid rgba(39, 49, 82, 1); }
  #signupdiv{position:absolute;top:15%;left:0%;width:100%; height:75%; background:white; border-radius:10px; box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 3px 0px, rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;}
  .signupinput{
	width:92%;margin-right:-5px;margin-left:auto;left:4%;position:relative;}
	.bigsignupinput{width: 92%;}
	#signupinputbox{ width:100%; height:50px;}
	lottie-player{
		display: none;
	}
	.signupinput > input::placeholder{
       color:rgba(115, 115, 115, 0.7); font-size:10px;
       }

     .checkdiv{margin-left:0px;}
   .button {
  margin-top:5px;
}

}